@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
p,a,h1,h2,h3{font-size:medium;font-size:initial;font-weight:500;letter-spacing:normal;letter-spacing:initial}ul,div{font-weight:500;letter-spacing:normal;letter-spacing:initial}p{line-height:1.55;text-align:left;margin-bottom:2rem}a{color:blue;text-decoration:underline;line-height:1.55}ul{padding:0;margin:0;list-style:none}ul li{font-size:medium;font-size:initial}ul li a{letter-spacing:normal;letter-spacing:initial;font-weight:500}a:hover{color:blue;font-weight:bold}::selection{color:#fff;background-color:#e10000}::-moz-selection{color:#fff;background-color:blue}.ft-s-s p,.ft-s-s a{font-size:medium;font-size:initial;line-height:1.55}.ft-s-ss p,.ft-s-ss a{font-size:medium;font-size:initial;line-height:1.55}.h-l{background-color:#ffeb37;color:black}.f-n{font-size:1.5vh;vertical-align:8px}.f-n-s{font-size:1.2vh;vertical-align:8px}.t-underline{text-decoration:underline}body{min-height:100vh;max-width:100vw;margin:0;background-color:#fff;overflow:hidden}.container{max-width:100% !important}.row{height:100%}.col-md-3{margin-bottom:40px;overflow:auto;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.layout{z-index:0;height:calc(100vh - 60px);width:100%;background-color:#fff}.view-layout{height:100%;width:100%;padding:80px 33px 0;background-color:#fff;overflow:auto;word-wrap:break-word}.mb-100{margin-bottom:100px}.pd-12{padding:0 12px}.outLine{border:2px solid black;padding:10px;overflow:auto;max-height:95%}.outLine-right{border-right:2px solid black;padding:10px}.bottomLine{display:inline;font-size:medium;font-size:initial;border-bottom:2px solid black}@media (min-width: 320px) and (max-width: 480px){.view-layout{padding:80px 20px 0}}

.footer{position:fixed;bottom:0;z-index:5;width:100%;text-align:center;font-size:1rem !important;margin-bottom:5px;font-weight:600;padding-bottom:0.5rem}.footer .lang-toggle-box{margin-bottom:0.2rem}.footer .lang-toggle-box .lang-toggle{margin:0 5px;cursor:pointer}.footer .lang-toggle-box .lang-toggle:hover{border-bottom:2px solid black}.footer .lang-toggle-box .active{border-bottom:2px solid black}

#Nav{z-index:5;position:fixed;top:6px;padding:5px 15px;display:flex;align-items:center;line-height:1;width:100%;overflow:auto;-ms-overflow-style:none;scrollbar-width:none;min-height:65px}#Nav .nav-toggle{margin:2px;font-weight:400;letter-spacing:2px;transition:all ease 0.1s 0s;-moz-transition:all ease 0.1s 0s;-webkit-transition:all ease 0.1s 0s;cursor:pointer}#Nav .nav-toggle h1{margin:0;font-weight:500;font-size:1.4rem}#Nav .nav-toggle a{margin-top:0.7rem;display:block}#Nav .active{border-bottom:2px solid black;filter:blur(0);-webkit-filter:blur(0)}#Nav .inactive{padding:4px 8px 4px 7px}#Nav .inactive h1{font-size:medium;font-size:initial}#Nav a{font-size:medium;font-size:initial}#Nav::-webkit-scrollbar{display:none}@media (min-width: 320px) and (max-width: 480px){#Nav{padding:2px 5px}}

.section{width:100%;height:100%;position:fixed;background-color:white;transition:all ease 0.3s 0s;-moz-transition:all ease 0.3s 0s;-webkit-transition:all ease 0.3s 0s}.visible{opacity:1}.invisible{opacity:0;transform:scale(0.95);-moz-transform:scale(0.95);-webkit-transform:scale(0.95);-ms-transform:scale(0.95)}

.about-mid{padding:80px 30px 40px;height:100vh;overflow:auto;overflow-x:hidden;top:-80px;text-align:center}.title-center{display:block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:auto}.about-center{text-align:center;word-break:keep-all}@media (min-width: 320px) and (max-width: 480px){.about-mid{padding:0 10px;height:auto;height:initial;top:auto;top:initial}}

.card-wrapper{margin:0 0 30px;padding:0 5px;color:black;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.card-wrapper a{color:black}.card-wrapper a h3{font-size:2.3vh}.card-wrapper a h3:hover{font-weight:bold}.card-wrapper .card{margin:0 0 15px;border:none;border-radius:0px;box-shadow:0.5vh 0.5vh 1vh #000000bd;width:100%;text-align:center;min-height:150px;display:flex;justify-content:center}.card-wrapper .card .empty-thumb{position:relative;height:0;padding-bottom:56.25%;display:flex;justify-content:center}.card-wrapper .card .empty-thumb span{position:absolute;top:45%}.card-wrapper .card .desc-card{z-index:20;padding:5px 7px;background-color:white;color:black;width:95%;height:95%;position:absolute;top:0;bottom:0;left:0;right:0;margin:auto;box-shadow:0.5vh 0.5vh 1vh #000000bd;overflow:auto;transition:all ease 0.3s 0s;-moz-transition:all ease 0.3s 0s;-webkit-transition:all ease 0.3s 0s}.card-wrapper .card .desc-card h3{text-align:center;font-size:1.4rem}.card-wrapper .card .desc-card p{margin-bottom:0.6rem}.card-wrapper .card .desc-card a{color:blue;text-align:left}.card-wrapper .card .desc-card .cate{text-align:center}.mobile-btn{font-size:2vh;border-bottom:2px solid;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;line-height:1}.ifram-wrapper{position:relative;height:0;padding-bottom:56.25%}.work-video{max-width:100%;position:absolute;width:100%;height:100%;left:0}.work-img{max-width:100%}@media (min-width: 320px) and (max-width: 480px){.card{box-shadow:0.2vh 0.2vh 0.5vh rgba(0,0,0,0.741176) !important}}

.row-px{padding:80px 10px 80px 20px;width:100%;height:100vh;overflow:auto;overflow-x:hidden;position:absolute;top:-80px}.row-px .scroll-info{width:100%;text-align:center;position:absolute;z-index:1;font-weight:bold;margin:auto;font-size:2rem;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}@media (min-width: 320px) and (max-width: 480px){.row-px{padding:0 0 20px;width:auto;width:initial;height:100%;position:static;position:initial}}


.work-layout{max-width:1200px;margin:auto;text-align:center;overflow:auto}.work-layout .container{padding:2rem 0}.work-layout .container p{text-align:center}

